:root {
    --primary: #7445ff;
    --secondary: #0a1f44;
    --primary-invert: #fff;
    --secondary-invert: #fff;
  
    /* BUTTONS COLOR */
    --purple-gradient: linear-gradient(180deg, #7445ff, #520df4);
    --dark-gradient: linear-gradient(230deg, #0a1f44, #020024);
  
    /* DEFAULT COLOR OF TEXTS */
    --text-primary: #0a1f44;
  
    /* FOR BUTTONS & INPUTS */
    --elements-roundness: 10rem;
  
    --space-between-blocks: 2rem;
    /* for mobiles */
    --space-between-blocks-small-screens: 3rem;
  }
  
  body {
    font-family: "Poppins", sans-serif;
    color: var(--text-primary);
  }
  
  .space-between-blocks {
    padding-top: var(--space-between-blocks-small-screens);
    padding-bottom: var(--space-between-blocks-small-screens);
  }
  
  @media (min-width: 992px) {
    .space-between-blocks {
      padding-top: var(--space-between-blocks);
      padding-bottom: var(--space-between-blocks);
    }
  }
  
  /* ----------------- BUTTONS ------------------- */
  
  button {
    color: inherit;
    padding: 0;
    background: none;
    border: none;
  }
  
  button:focus {
    outline: none;
    box-shadow: none;
  }
  
  .btn {
    font-size: 0.87rem;
    padding: 0.8rem 1.6rem;
    border-radius: var(--elements-roundness);
    transition: 0.2s all;
  }
  
  @media (min-width: 992px) {
    .btn {
      padding: 0.8rem 2rem;
    }
  }
  
  .btn,
  .btn:hover,
  .btn:focus {
    border: none;
    box-shadow: none;
    outline: none;
  }
  
  .btn:hover,
  .btn:focus {
    transform: scale(1.05);
  }
  
  .btn-primary,
  .btn-primary:hover,
  .btn-primary:focus {
    background: var(--primary);
    color: var(--primary-invert);
  }
  
  .btn-secondary,
  .btn-secondary:hover,
  .btn-secondary:focus {
    background: var(--secondary);
    color: var(--secondary-invert);
  }
  
  .btn-sm {
    padding: 0.8rem;
  }
  
  /* Put your blocks here */
  
  /* HERO */
  .hero {
    --hero-nav-height: 100px;
    --block-background: var(--primary);
    --block-text-color: var(--primary-invert);
    background: var(--block-background);
    color: var(--block-text-color);
    position: relative;
    display: flex;
    flex-direction: column;
  }
  
  .hero-nav {
    margin: 0 -1rem;
    width: 100%;
    height: var(--hero-nav-height);
    display: flex;
    align-items: center;
    z-index: 2;
  }
  
  .hero-nav__item {
    text-align: center;
    font-size: 1.1rem;
  }
  
  @media (min-width: 992px) {
    .hero-nav__item {
      margin: 0 1rem;
    }
  }
  
  @media (min-width: 992px) {
    .nav--lg-side {
      flex-direction: row-reverse;
    }
  }
  
  .hero-nav__logo {
    height: 35px;
  }
  
  .hero-nav__link {
    color: inherit;
    opacity: 0.9;
  }
  
  .hero-nav__link:focus,
  .hero-nav__link:hover {
    color: var(---primay-invert);
    text-decoration: none;
    opacity: 1;
  }
  
  .hero__row {
    height: 100%;
  }
  
  .hero_empty-column {
    height: 100%;
  }
  
  .hero__image-column {
    height: 100%;
    background-position: center;
    background-size: cover;
  }
  
  .hero__content {
    text-align: initial;
    padding-top: 2rem;
  }
  
  @media (min-width: 992px) {
    .hero__content {
      padding-top: 7rem;
      padding-bottom: 2.5rem;
    }
  }
  
  .hero__title {
    font-size: 1.8rem;
    font-weight: 900;
  }
  
  .hero__paragraph {
    font-size: 0.9rem;
    font-weight: 400;
    opacity: 0.9;
  }
  
  @media (min-width: 576px) {
    .hero__title {
      font-size: 2.5rem;
      line-height: 1.17;
    }
  
    .hero__paragraph {
      width: 80%;
      margin: auto;
    }
  }
  
  @media (min-width: 768px) {
    .hero__title {
      font-size: 3rem;
    }
  }
  
  @media (min-width: 992px) {
    .hero__title {
      font-size: 2.5rem;
      line-height: 1.17;
    }
  
    .hero__paragraph {
      font-size: 1.5rem;
      width: 70%;
    }
  }
  
  @media (min-width: 1200px) {
    .hero__title {
      font-size: 3.5rem;
      line-height: 1.17;
    }
  }
  
  .hero__btns-container {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    width: 150px;
    margin: auto;
  }
  
  .hero__btn {
    display: inline-block;
    width: 100%;
  }
  
  @media (min-width: 992px) {
    .hero__btns-container {
      width: auto;
      flex-direction: row;
    }
  
    .hero__btn {
      width: auto;
    }
  }
  
  .highlight {
    color: var(--primary);
  }
  
  .ft-menu {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 2000;
    -webkit-animation-name: fadeOut;
    animation-name: fadeOut;
    -webkit-animation-duration: 0.5s;
    animation-duration: 0.5s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
  }
  
  @media (print), (prefers-reduced-motion: reduce) {
    .ft-menu {
      -webkit-animation-duration: 1ms !important;
      animation-duration: 1ms !important;
      -webkit-transition-duration: 1ms !important;
      transition-duration: 1ms !important;
      -webkit-animation-iteration-count: 1 !important;
      animation-iteration-count: 1 !important;
    }
  }
  
  .ft-menu .hero-nav__item {
    width: 100%;
    margin-top: 0.75rem;
    margin-bottom: 0.75rem;
  }
  
  @media (min-width: 992px) {
    .ft-menu .hero-nav__item {
      width: auto;
      margin-top: 0;
      margin-bottom: 0;
    }
  }
  
  .ft-menu--js-show {
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
    -webkit-animation-duration: 0.5s;
    animation-duration: 0.5s;
  }
  
  .ft-menu::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: var(--primary);
    opacity: 0.9;
    z-index: -1;
  }
  
  @media (min-width: 992px) {
    .ft-menu {
      -webkit-animation-name: none;
      animation-name: none;
      position: static;
      z-index: auto;
    }
  
    .ft-menu::before {
      content: none;
    }
  }
  
  .ft-menu__slider {
    width: 80%;
    height: 100%;
    background: var(--block-background);
    overflow: hidden;
    transform: translateX(-100%);
    transition: 0.5s transform;
  }
  
  @media (min-width: 992px) {
    .ft-menu__slider {
      width: auto;
      height: auto;
      background: 0 0;
      transform: none;
      display: flex;
      align-items: center;
    }
  }
  
  .ft-menu--js-show .ft-menu__slider {
    transform: translateX(0);
  }
  
  .ft-menu__close-btn {
    color: var(--primary-invert);
    position: absolute;
    right: 0;
    top: 0;
    font-size: 2rem;
    margin: 1rem;
    transform: translateX(100%);
    transition: 0.5s transform;
  }
  
  @media (min-width: 992px) {
    .ft-menu__close-btn {
      display: none;
    }
  }
  
  .ft-menu--js-show .ft-menu__close-btn {
    transform: translateX(0);
  }
  
  .ft-menu__close-btn:focus,
  .ft-menu__close-btn:hover {
    color: var(--primary-invert);
  }
  
  .pattern {
    position: absolute;
    height: 47%;
    width: 17%;
    opacity: 0.5;
  }
  
  .pattern--primary {
    color: var(--primary);
  }
  
  .pattern--left-bottom {
    bottom: 0;
    left: 0;
  }
  
  .pattern--right {
    top: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }
  
  @-webkit-keyframes fadeIn {
    from {
      visibility: hidden;
      opacity: 0;
    }
  
    to {
      visibility: visible;
      opacity: 1;
    }
  }
  
  @keyframes fadeIn {
    from {
      visibility: hidden;
      opacity: 0;
    }
  
    to {
      visibility: visible;
      opacity: 1;
    }
  }
  
  @-webkit-keyframes fadeOut {
    from {
      visibility: visible;
      opacity: 1;
    }
  
    to {
      visibility: hidden;
      opacity: 0;
    }
  }
  
  @keyframes fadeOut {
    from {
      visibility: visible;
      opacity: 1;
    }
  
    to {
      visibility: hidden;
      opacity: 0;
    }
  }
  
  /* TESTIMONIALS */
  .block-20 {
    --block-background: var(--purple-gradient);
    --block-text-color: var(--text-primary);
    background: var(--block-background);
    color: var(--block-text-color);
  }
  
  .block__header {
    margin-bottom: 4rem;
  }
  
  .block__pre-title {
    color: var(--primary);
    font-size: 1rem;
    font-weight: 600;
  }
  
  .block__title {
    font-size: 2rem;
    font-weight: 900;
    line-height: 1.4;
  }
  
  @media (min-width: 992px) {
    .block__title {
      font-size: 2.5rem;
    }
  }
  
  .block__paragraph {
    font-size: 1.1rem;
    line-height: 1.5;
    opacity: 0.9;
  }
  
  .testimonial-card-1 {
    padding: 1.5rem;
    padding-top: 10rem;
    margin-top: 58px;
    margin-bottom: 25px;
    background: #fff;
    border: 1px solid #dee2e6;
    border-radius: 5px;
    position: relative;
  }
  
  .testimonial-card-1__paragraph {
    font-size: 0.87rem;
    opacity: 0.9;
    line-height: 1.7;
  }
  
  .testimonial-card-1__quote-symbol {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    background: var(--primary);
    color: var(--primary-invert);
    font-size: 1.3rem;
    border: 2px solid #fff;
    border-radius: 10rem;
    position: absolute;
    bottom: -25px;
    left: 0;
    right: 0;
    margin: auto;
  }
  
  .person {
    text-align: center;
    position: absolute;
    top: -58px;
    left: 0;
    right: 0;
    margin: auto;
  }
  
  .person__avatar {
    padding: 6px;
    width: 116px;
    height: 116px;
    background: #fff;
    border-radius: 10rem;
    border: solid 2px #eff0f9;
  }
  
  .person__rating {
    font-size: 0.75rem;
    color: #ffe200;
  }
  
  .person__name {
    font-size: 0.87rem;
    font-weight: 600;
  }
  
  .person__info {
    font-size: 0.8rem;
    font-weight: 600;
    opacity: 0.6;
  }
  